import { Handler, Context, APIGatewayProxyEvent, APIGatewayProxyResult } from 'aws-lambda';

export const handler: Handler<APIGatewayProxyEvent, APIGatewayProxyResult> = async (
  event: APIGatewayProxyEvent,
  context: Context
): Promise<APIGatewayProxyResult> => {
  console.log('LAMBDA PROXY (SIMPLIFIED): Event received:', JSON.stringify(event, null, 2));
  console.log('LAMBDA PROXY (SIMPLIFIED): Context received:', JSON.stringify(context, null, 2));

  return {
    statusCode: 200,
    body: JSON.stringify({
      message: "LAMBDA PROXY (SIMPLIFIED) WAS INVOKED!",
      invoked_function_arn: context.invokedFunctionArn,
    }),
    headers: { 'Content-Type': 'application/json' },
  };
};
